import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {useNavigate} from "react-router-dom";

const StartPage = () => {
    const [currentTime, setCurrentTime] = useState(new Date());
    const [name, setName] = useState('');
    const [initialMoney, setInitialMoney] = useState('');
    const nav = useNavigate();
    // State lu li
    const [errors, setErrors] = useState({ name: '', money: '' });

    useEffect(() => {
        const timer = setInterval(() => {
            setCurrentTime(new Date());
        }, 1000);
        return () => clearInterval(timer);
    }, []);

    // Format tin VND vi du chm
    const formatMoney = (value) => {
        if (!value) return '';
        return Number(value).toLocaleString('vi-VN');
    };

    const handleClick = () => {
        let valid = true;
        const newErrors = { name: '', money: '' };

        if (!name.trim()) {
            newErrors.name = 'Vui lng nhp h tn!';
            valid = false;
        }

        if (!initialMoney || Number(initialMoney) <= 0) {
            newErrors.money = 'Vui lng nhp s tin hp l!';
            valid = false;
        }

        setErrors(newErrors);

        if (valid) {
            // Lu vo localStorage
            localStorage.setItem('startName', name);
            localStorage.setItem('startMoney', initialMoney); // lu s nguyn
            nav('/export-pdf');
            // alert(` lu:\nH tn: ${name}\nTin u ca: ${formatMoney(initialMoney)} VND\nThi gian: ${currentTime.toLocaleString('vi-VN')}`);
        }
    };

    return (
        <div
            className="d-flex vh-100 justify-content-center align-items-center p-3"
            style={{ background: 'linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)' }}
        >
            <div
                className="card shadow w-100"
                style={{ maxWidth: '480px', background: 'linear-gradient(145deg, #ffffff 0%, #e6f0ff 100%)' }}
            >
                <div className="card-body">
                    <h2 className="card-title text-center mb-4">Kt Ka </h2>
                    {/* ng h */}
                    <div className="mb-4 text-center">
                        <p className="text-secondary fs-6 mb-1">{currentTime.toLocaleDateString('vi-VN')}</p>
                        <p className="fw-bold text-primary fs-3 mb-0">{currentTime.toLocaleTimeString('vi-VN')}</p>
                    </div>

                    {/* H tn */}
                    <div className="mb-4">
                        <label className="form-label fw-bold fs-5">Nhn vin</label>
                        <input
                            type="text"
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                            className={`form-control form-control-lg ${errors.name ? 'is-invalid' : ''}`}
                            placeholder="Nhp tn nhn vin..."
                        />
                        {errors.name && <div className="text-danger mt-1">{errors.name}</div>}
                    </div>

                    {/* Tin u ca */}
                    <div className="mb-4">
                        <label className="form-label fw-bold fs-5">Tin u ca (VND)</label>
                        <input
                            type="text"
                            value={formatMoney(initialMoney)}
                            onChange={(e) => setInitialMoney(e.target.value.replace(/\D/g, ''))} // ch gi s
                            className={`form-control form-control-lg ${errors.money ? 'is-invalid' : ''}`}
                            placeholder="Nhp u ca..."
                        />
                        {errors.money && <div className="text-danger mt-1">{errors.money}</div>}
                    </div>

                    <div className="d-grid mt-4">
                        <button className="btn btn-primary btn-lg" onClick={handleClick}>
                            OK
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default StartPage;
